:root {
    --color-primary: #F23847;
    --color-secondary: #A62934;
    --color-bg: #8B8B8C;
    --color-bg2: #404040;
    --color-shadow: #323232;
    --color-accent1: #404040;
    --color-accent2: #0D0D0D;
    --color-text: #0D0D0D;
    --color-text-dark: #202020;
    --font-sans: "Inter", system-ui, -apple-system, "Segoe UI", Roboto, Ubuntu, Cantarell, "Noto Sans", Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
    --font-heading: "Poppins", var(--font-sans);
    --phone-column-size: 80vw;
}

* {
    -webkit-tap-highlight-color: transparent;
}

#root, .App {
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-sans);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-heading);
    font-weight: 700;
    letter-spacing: 0.01em;
}

.skills {
    display: flex;
    flex-wrap: wrap;
}
.skill {
    display: inline-block;
    border-radius: 999px;
    font-weight: 700;
    letter-spacing: 0.01em;
    color: var(--color-accent2);
    background: color-mix(in oklab, var(--color-bg2) 10%, white);
    border: 1px solid color-mix(in oklab, var(--color-shadow) 18%, transparent);
}
.skill.is-code {
    background: color-mix(in oklab, var(--color-secondary) 30%, white);
}
.skill.is-app  {
    background: color-mix(in oklab, var(--color-accent1) 30%, white);
}
.skill.is-tool {
    background: color-mix(in oklab, var(--color-primary) 60%, white);
}

a, button, .button {
    -webkit-tap-highlight-color: transparent;
}
.button {
    --btn-bg: var(--color-primary);
    --btn-fg: #fff;
    border-radius: 0.8em;
    background: linear-gradient(180deg, color-mix(in oklab, var(--btn-bg) 92%, white), var(--btn-bg));
    color: var(--btn-fg);
    font-weight: 700;
    letter-spacing: 0.02em;
    cursor: pointer;
    box-shadow: 0 0.35rem 0.9rem color-mix(in oklab, var(--color-shadow) 28%, transparent);
    transition: transform 120ms ease, filter 160ms ease, box-shadow 160ms ease;
    text-decoration: none;
}
.button:active {
    transform: translateY(0);
}
.button:focus-visible {
    outline: none;
    box-shadow:
            0 0.35rem 0.9rem color-mix(in oklab, var(--color-shadow) 28%, transparent),
            0 0 0 0.2rem color-mix(in oklab, var(--btn-bg) 35%, transparent);
}
@media (hover: hover) and (pointer: fine){
    .button:hover {
        filter: brightness(1.06) saturate(1.05);
        transform: translateY(-1px);
        --btn-bg: var(--color-secondary);
    }
}
@media (max-width: 50rem) {
    .button:active {
        filter: brightness(1.06) saturate(1.05);
        transform: translateY(-1px);
        --btn-bg: var(--color-secondary);
    }
}