.pcard {
    position: relative;
    isolation: isolate;
    border-radius: 0.9rem;
    overflow: hidden;
    cursor: pointer;
    min-block-size: clamp(15rem, 32vw, 19rem);
    background: color-mix(in oklab, var(--color-bg2) 70%, black);
    box-shadow: 0 0.45rem 1.2rem color-mix(in oklab, var(--color-shadow) 35%, transparent);
    transition: transform 160ms ease, box-shadow 180ms ease;
    outline: none;
}
.pcard:focus-visible {
    box-shadow:
            0 0 0 0.2rem color-mix(in oklab, var(--color-primary) 35%, transparent),
            0 0.7rem 1.6rem color-mix(in oklab, var(--color-shadow) 45%, transparent);
}
.pcard__bg {
    position: absolute;
    inset: 0;
    z-index: 0;
}
.pcard__img {
    inline-size: 100%; block-size: 100%;
    object-fit: cover; display: block;
    filter: saturate(1.05) contrast(1.05);
    transform: scale(1.02); transition: transform 400ms ease;
}

.pcard__shade {
    position: absolute; inset: 0; z-index: 1;
    background: linear-gradient(180deg, rgba(0,0,0,0.0) 0%, rgba(0,0,0,0.45) 55%, rgba(0,0,0,0.75) 100%);
}

.pcard__content {
    position: absolute;
    left: 0; right: 0; bottom: 0;
    z-index: 2;
    padding: clamp(0.9rem, 1.7vw, 1.15rem);
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.25) 60%, rgba(0,0,0,0.4) 100%);
}

.pcard__title {
    margin: 0;
    color: #fff;
    font-weight: 800;
    letter-spacing: 0.01em;
    font-size: clamp(1.05rem, 0.95rem + 0.7vw, 1.4rem);
    text-shadow: 0 0.35rem 0.9rem rgba(0,0,0,0.55);
    border-inline-start: 0.25rem solid var(--color-primary);
    padding-inline-start: 0.6rem;
    padding-block: 0.1rem 0.05rem;
    line-height: 1.2;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.pcard:not(:has(img)) .pcard__shade {
    background: linear-gradient(180deg, rgba(0,0,0,0.1), rgba(0,0,0,0.3));
}

@media (max-width: 35rem) {
    .pcard {
        min-block-size: clamp(10.5rem, 36vw, 12.5rem);
    }
    .pcard__content {
        padding: clamp(0.65rem, 3.2vw, 0.85rem);
    }
    .pcard__title {
        font-size: clamp(0.95rem, 0.8rem + 0.6vw, 1.1rem);
        -webkit-line-clamp: 2;
    }
}
@media (hover: hover) and (pointer: fine){
    .pcard:hover {
        transform: translateY(-0.2rem);
        box-shadow: 0 0.7rem 1.6rem color-mix(in oklab, var(--color-shadow) 45%, transparent);
    }
    .pcard:hover .pcard__img {
        transform: scale(1.06);
    }
}
@media (max-width: 50rem) {
    .pcard:active {
        transform: translateY(-0.2rem);
        box-shadow: 0 0.7rem 1.6rem color-mix(in oklab, var(--color-shadow) 45%, transparent);
    }
    .pcard:active .pcard__img {
        transform: scale(1.1);
    }
}